.fb-testimonial-section {
    display: block;
    transition: ease-in-out 0.5s;

    &.fb-testimonial-section-left {
        text-align: left;
    }

    &.fb-testimonial-section-center {
        text-align: center;
    }
    &.fb-testimonial-section-right {
        text-align: right;
    }

    .fb-testimonial-wrapper {
        width: 100%;
    }

    .fb-testimonial-rating {
        .fb-testimonial-rating-icon {
            display: inline-block;
        }
        svg {
            transition: ease-in-out 0.5s;
        }
    }

    .fb-testimonial-author-container {
        display: flex;
        align-items: center;
        &.left {
           justify-content: flex-start;
        }
        &.center {
            justify-content: center;
        }
        &.right {
            justify-content: flex-end;
        }
        .fb-testimonial-author-inner-container {
            display: flex;
            align-items: center;
            .fb-testimonial-author-image-container {
                .fb-testimonial-image {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img {
                        max-width: 100%;
                    }
                }
            }
    
            .fb-testimonial-userinfo-container {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
        }
    }

    .fb-testimonial-quote-icon {
        display: inline-block;
        svg {
            transition: ease-in-out 0.5s;
        }
    }
}
